import React, { Component } from 'react';
import StudentsListDisplay from './StudentsListDisplay';

import axios from 'axios';
import { getSingleUser } from '../../../../services/userService';

class StudentsListContainer extends Component {

  state = {
    data: [],
    student:{},
    classrooms:[],
    visible: false,
    columns: [
      { title: 'Name', dataIndex: 'name', key: 'name' },
      { title: 'Email', dataIndex: 'email', key: 'email' },
      { title: 'Classroom', dataIndex: 'classroom', key: 'classroom' , render:(i, obj)=>(<p>{obj.classroom.name}</p>)},
      { title: 'Empe√±o', dataIndex: 'empe√±o', key: 'empe√±o' },
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.showModal(obj._id)}>Edit</button>)},
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.removeStudent(obj._id)}>Delete</button>)}
    ]
  }

  onChange = (e) => {
    const field = e.target.name
    const value = e.target.value
    const {student} = this.state
    student[field] = value
    this.setState({student})
}

  showModal = (id) => {
    console.log(id)
    getSingleUser(id)
    .then(student=>{
      this.setState({student, visible:true})
    }).catch(e=>{
      console.log(e)
    })
  
  }

  handleOk = (e) => {
    console.log(e);
    //mandar el student al backend
    this.editStudent()
    this.setState({
      visible: false,
    });
  }

  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }

  componentDidMount() {
    axios.get('https://eci-campus-app.herokuapp.com/admin/manageStudents',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    })
      .then(res => {
        console.log(res.data)
        this.setState({
          data: res.data})
      })

      axios.get('https://eci-campus-app.herokuapp.com/admin/classroomsSelect',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res => {
        this.setState({
          classrooms: res.data})
      }).catch(e=>{
        console.log(e)
      })

  }


  removeStudent=(_id)=>{    
    axios.post('https://eci-campus-app.herokuapp.com/admin/deleteStudent',{_id},{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res =>{
      console.log('se borro!')
      let {data} = this.state
      data = data.filter(d=>d._id!==_id)
      this.setState({data})
    })
    .catch(e=>{
      console.log(e)
    })
  }

  editStudent=()=>{    
    const {student} = this.state
    axios.post('https://eci-campus-app.herokuapp.com/admin/editStudent/' + student._id, student, {
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res =>{
      let {data} = this.state
      data = data.map(d=>{
        if(d._id===student._id) return student
        return d
      })
      this.setState({data})
    })
    .catch(e=>{
      console.log(e)
    })
  }

  render() {
    const {columns, data, visible, classrooms} = this.state
    console.log(this.state.data)
    return (
      <div>
        <StudentsListDisplay onChange={this.onChange} classrooms={classrooms} editStudent={this.editStudent} student={this.state.student} columns={columns} data={data} visible={visible} handleOk={this.handleOk} handleCancel={this.handleCancel} />
      </div>
    );
  }
}

export default StudentsListContainer;